body {
  margin: 0;
  padding: 0;
  background-image: url(/cage-the-elephant/static/media/background-tile.3b1593ab.png);
  background-repeat: repeat, repeat;
}

#header{
	background-color: rgba(5, 0, 0, 0.7);
	min-height: 50px;
	position: fixed;
    z-index: 1;
	top: 0;
	width: 100%;
	display: -webkit-flex;
	display: flex;
	padding: 2px;
	-webkit-justify-content: space-between;
	        justify-content: space-between;
}

#img-logo{
	width: 60px;
	height: 60px;
	padding: 5px;
	margin-top: 10px;
}

#social{
	font-size: 1.2em;
	margin: 2px;
	width: 8%;
	display: -webkit-flex;
	display: flex;
}

#container-logo{
	width: 10%;
	display: -webkit-flex;
	display: flex;
	-webkit-justify-content: flex-end;
	        justify-content: flex-end;
}

#section-name{
	width: 30%;
	text-align: center;
	color: #E15634;
	display: -webkit-flex;
	display: flex;
	text-transform: uppercase;
}

#section-name h1 {
	margin: auto;
}

@media only screen and (max-width: 768px) {
  #header{
  	font-size: 1em;
  }
  #social{
  	width: 40%;
  	margin: 10px;
  	background-color: blue;
  }

}
#content-navbar{ 
	position: fixed;
	top: 30%;
	left: 0;
	width: 50px;
	z-index: 1;
	border-radius: 0px 16px 16px 0px;
	min-height: 250px;
	background-color: rgba(5, 0, 0, 0.7);
  color: white;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-justify-content: space-between;
          justify-content: space-between;
}

#content-navbar section{
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  height: 35px;
  padding: 7px;
  font-size: 20px;
}

.nav{
  margin: auto;
  cursor: pointer;
  color: #E15634;
}

.nav:hover{
  color: #441A0F;
}

.selected{
	background-color: white;
}

@media only screen and (max-width: 768px) {
  #content-navbar{
   	top: 100%;
    bottom: 0;
   	width: 100%;
   	min-height: 40px;
    height: 40px;
    max-height: 40px;
   	border-radius: 0;  
    -webkit-flex-direction: row;  
            flex-direction: row;
    font-size: 1.2em;
  }
  
}

@font-face {
  font-family: 'Quicksand';
  font-style: normal;
  font-display: swap;
  font-weight: 400;
  src: local('Quicksand Regular'), local('Quicksand-Regular'), url(https://fonts.gstatic.com/s/quicksand/v7/6xKtdSZaM9iE8KbpRA_hK1QNYuDyPw.woff2) format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2191, U+2193, U+2212, U+2215, U+FEFF, U+FFFD;
}
.App {
  height: 100%;
  width: 100%;
  font-family: "Quicksand", sans-serif;
}

.container-position{
	min-width: 100vh;
  margin: 0;
  min-height: 100vh;
  max-height: 100vh;
  display: -webkit-flex;
  display: flex;
}

.shadow{
   box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

@media only screen and (max-width: 768px) {
  .container-position{   
    min-width: 100%;
    width: 100%;
    max-width: 100%;
    height: 85vh;
    max-height: 85vh;
    overflow-x:hidden;
    margin-top: 10px;background-color: yellow;
  }
}


