#header{
	background-color: rgba(5, 0, 0, 0.7);
	min-height: 50px;
	position: fixed;
    z-index: 1;
	top: 0;
	width: 100%;
	display: flex;
	padding: 2px;
	justify-content: space-between;
}

#img-logo{
	width: 60px;
	height: 60px;
	padding: 5px;
	margin-top: 10px;
}

#social{
	font-size: 1.2em;
	margin: 2px;
	width: 8%;
	display: flex;
}

#container-logo{
	width: 10%;
	display: flex;
	justify-content: flex-end;
}

#section-name{
	width: 30%;
	text-align: center;
	color: #E15634;
	display: flex;
	text-transform: uppercase;
}

#section-name h1 {
	margin: auto;
}

@media only screen and (max-width: 768px) {
  #header{
  	font-size: 1em;
  }
  #social{
  	width: 40%;
  	margin: 10px;
  	background-color: blue;
  }

}